import { FrameContext } from '../frame';

/**
 * FrameComponent
 */
class FrameComponentRefresher {

    private frameComponentMap: Map<string, any>;

    /**
     * 
     */
    constructor() {
        this.frameComponentMap = new Map<string, any>();
    }

    /**
     * 
     */
    public refreshComponents() {
        this.frameComponentMap.forEach((component: any, frameId: string) => {
            if (typeof component['onFormLoad'] === 'function') {
                component.onFormLoad();
            }
        });
    }

    /**
     * 
     * @param frameId   FrameId
     * @param component Frame
     */
    public regFrameComponent(frameId: string, component: any) {
        this.frameComponentMap.set(frameId, component);
    }
    /**
     * 
     */
    public unregFrameContext(frameContext: FrameContext): void {
        const frameId = frameContext.frameId;
        this.frameComponentMap.delete(frameId);
    }
}

export { FrameComponentRefresher };
